.van-divider {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-style: solid;
  border-width: 0;
  margin: 0.68267rem 0;
  margin: var(--divider-margin, 0.68267rem 0);
  color: #969799;
  color: var(--divider-text-color, #969799);
  font-size: 0.59733rem;
  font-size: var(--divider-font-size, 0.59733rem);
  line-height: 1.024rem;
  line-height: var(--divider-line-height, 1.024rem);
  border-color: #ebedf0;
  border-color: var(--divider-border-color, #ebedf0);
}
.van-divider::before,
.van-divider::after {
  display: block;
  -webkit-flex: 1;
          flex: 1;
  box-sizing: border-box;
  height: 0.04267rem;
  border-color: inherit;
  border-style: inherit;
  border-width: 0.04267rem 0 0;
}
.van-divider::before {
  content: '';
}
.van-divider--hairline::before,
.van-divider--hairline::after {
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.van-divider--dashed {
  border-style: dashed;
}
.van-divider--center::before,
.van-divider--left::before,
.van-divider--right::before {
  margin-right: 0.68267rem;
  margin-right: var(--divider-content-padding, 0.68267rem);
}
.van-divider--center::after,
.van-divider--left::after,
.van-divider--right::after {
  content: '';
  margin-left: 0.68267rem;
  margin-left: var(--divider-content-padding, 0.68267rem);
}
.van-divider--left::before {
  max-width: 10%;
  max-width: var(--divider-content-left-width, 10%);
}
.van-divider--right::after {
  max-width: 10%;
  max-width: var(--divider-content-right-width, 10%);
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
